@import "../../../index.scss";


.button{
  width: 83px;
  height: 22px;
  border-radius: 20px;
  border: solid 1px $PrimaryBorderColor;
  cursor: pointer;
  text-align: center;
  font-size: 12px; 
  display: flex;
  justify-content: center;
  display: flex;
  margin-left: 9px;
  background: transparent;
 &:hover {
  background-color: $PrimaryBgColor;
  color: #fff;
  }
}